/**
 *  Animates content section to fade in and upwards on down scroll
 *
 * @module paint-layouts/home
 * @author Anett Soos <anett.soos@city.ac.uk>
 * @copyright City, University of London 2022
 */

const className = 'fade-out-scroll';

function addEventFadeOutScroll () {
    console.log('js worked AS');
}

function handleIntersect(elements) {
    for (const element of elements) {
        if (element.intersectionRatio < 0) {
            element.target.classList.add('in-view');
        }

        if (element.intersectionRatio > 0) {
            element.target.classList.remove('in-view');
        }
    }
}

function launchAddClass(addClass) {
    window.addEventListener('scroll', () =>
        new IntersectionObserver(handleIntersect).observe(addClass)
    );
}

export default {
    launchFn: addEventFadeOutScroll,
    launchQuery: `.${className}`,
};
